export default {
	data() {
		return {
			additionalType: 0,
			carType: 0,
			car: {
				load: '',
				size: '',
				volume: ''
			},
			send: '',
			take: '',
			isShow: true,
			location: {
				startLong: '',
				startLat: '',
				endLong: '',
				endLat: ''
			},
			price: 0,
			distance: 0
		}
	},
	onLoad() {
		this.getCar("面包车");
	},
	methods: {
		/*
		 * 导航栏切换
		 */
		changeType(e) {
			switch (e) {
				case 0:
					this.getCar("面包车");
					break;
				case 1:
					this.getCar("小平板车");
					break;
				case 2:
					this.getCar("中面包车");
					break;
				case 3:
					this.getCar("厢型货车");
					break;
				case 4:
					this.getCar("大型平板");
					break;
			}
			this.carType = e;
			this.isShow = e === 0;
		},
		/*
		 * 面包车独有附加选项
		 */
		additional(e) {
			this.additionalType = e;
		},
		/*
		 * 获取位置详情
		 */
		getLocation(type) {
			let _this = this;
			uni.chooseLocation({
				success: function(res) {
					if (type === 1) {
						_this.send = res.address;
						_this.location.startLong = res.longitude;
						_this.location.startLat = res.latitude;
					} else {
						_this.take = res.address;
						_this.location.endLong = res.longitude;
						_this.location.endLat = res.latitude;
					}
					_this.isUse();
				}
			});
		},
		/*
		 * 获取上方车辆信息
		 */
		getCar(name) {
			let _this = this;
			_this.request({
				url: _this.http.getTransportList,
				data: {
					pageNum: '1',
					pageSize: '1',
					transportName: name
				},
				success: (res) => {
					if (res.code === '0') {
						_this.car = res.result[0];
						_this.isUse();
					}
				}
			});
		},
		/*
		 * 计算价格
		 */
		countPrice(data) {
			let _this = this;
			_this.request({
				url: _this.http.priceBudget,
				data: {
					orderType: '1',
					transportId: _this.car.transportId,
					startLong: data.startLong,
					startLat: data.startLat,
					endLong: data.endLong,
					endLat: data.endLat
				},
				success: (res) => {
					if (res.code === '0') {
						_this.price = res.result.price;
						_this.distance = res.result.distance;
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		/*
		 * 判断是否调用计算价格
		 */
		isUse() {
			if (this.location.startLong && this.location.startLat && this.location.endLong && this.location.endLat) {
				this.countPrice(this.location);
			}
		},
		/*
		 * 去订单页面
		 */
		toOrder(e){
			if (this.send === '') {
				return uni.showToast({
					title: "请选择发货地",
					icon: 'none'
				});
			}
			if (this.take === '') {
				return uni.showToast({
					title: "请选择收货地",
					icon: 'none'
				});
			}
			let params = {
				transportId: this.car.transportId,
				startPlace: this.send,
				startLong: this.location.startLong,
				startLat: this.location.startLat,
				endPlace: this.take,
				endLong: this.location.endLong,
				endLat: this.location.endLat,
				type: e,
				price: this.price,
				distance: this.distance
			};
			if(this.carType === 0){
				params.additionalType = this.additionalType;
			}
			uni.navigateTo({
				url: '/pages/confirmOrder/confirmOrder?params=' + JSON.stringify(params)
			});
		}
	}
}
